@font-face {
  font-family: neon;
  src: url("../static/neon/NEON CLUB MUSIC.otf"); }

@font-face {
  font-family: "San Francisco";
  src: url("../static/SF-UI/SF-UI-Display-Regular.otf"); }

* {
  font-family: "San Francisco"; }

img {
  width: 100%;
  height: 100%; }

#menu-header {
  display: flex;
  justify-content: center; }

.ant-col {
  padding: 0 15px; }

.equal {
  display: flex;
  display: -webkit-flex;
  flex-wrap: wrap; }

.align-center {
  text-align: center; }

.flex-content-center {
  display: flex;
  justify-content: center;
  align-items: center; }

.median-font {
  font-size: 1.5rem; }

.default-color {
  color: #314659; }

.mt-3 {
  margin-top: 3rem; }

.mt-1 {
  margin-top: 1rem; }

.mt-2 {
  margin-top: 2rem; }

.m-horizontal-1 {
  margin-left: 0.5rem;
  margin-right: 0.5rem; }

.banner-wrapper {
  max-width: 1500px;
  margin: auto; }

.ant-carousel .slick-dots li.slick-active button {
  background: #314659; }

.ant-carousel .slick-dots li button {
  background: #5b6d7d; }

.ant-carousel .slick-slide {
  border-radius: 10px;
  text-align: left;
  min-height: 160px;
  background: #f2f4f5;
  overflow: hidden;
  padding: 2rem 4rem;
  font-size: 1rem; }

.ant-carousel .slick-slide h3 {
  color: #f5222d; }

.page-content-2 {
  max-width: 840px;
  margin: auto; }
  .page-content-2 .title {
    text-align: center;
    font-size: 1.5rem;
    margin-top: 1rem;
    margin-bottom: 1rem; }

footer {
  text-align: center; }
